@font-face {
  font-family: 'OpensSans';
  src: url('../fonts/opensans-regular.woff2') format('woff2'),
    url('../fonts/opensans-regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'OpensSans';
  src: url('../fonts/opensans-semibold.woff2') format('woff2'),
    url('../fonts/opensans-semibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'OpensSansItalic';
  src: url('../fonts/opensans-italic.woff2') format('woff2'),
    url('../fonts/opensans-italic.woff') format('woff');
  font-weight: 400;
  font-style: cursive;
}

@font-face {
  font-family: 'PlayfairDisplay';
  src: url('../fonts/playfairdisplay-regular.woff2') format('woff2'),
    url('../fonts/playfairdisplay-regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

html,
body {
  position: relative;
  box-sizing: border-box;
  max-width: 100%;
  min-width: $mobile-width;
  overflow-x: hidden;
  @include text;
}

body {
  background-color: $main-bg;
  margin: 0;
}

button {
  cursor: pointer;
  transition: 0.3s;

  &:focus,
  &:hover {
    outline: none;
    opacity: 0.9;
  }

  &:active {
    opacity: 0.7;
  }
}

a {
  text-decoration: none;
  @include text;
  color: $blue-dark;
  transition: 0.3s;

  &:focus,
  &:hover {
    outline: none;
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $blue-black;
}

em {
  font-family: "OpensSansItalic", sans-serif;
}

.visually-hidden {
  @include visually-hidden;
}

.fixed {
  width: 100%;
  position: fixed;
  z-index: 2;
}

.static {
  position: static;
}

.wrap {
  @media (min-width: 1440px) {
    width: 1340px;
    margin: 0 auto;
  }
}