@mixin input {
  display: block;
  width: 100%;
  padding: 12px;
  padding-top: 24px;
  padding-bottom: 24px;
  border: none;
  text-align: left;
  font-size: 16px;
  line-height: 20px;
  font-family: $font-main;
  font-weight: 400;
  background-color: $white;
  color: #474747;

  box-shadow: 0 0 0 1px $text-light;
  cursor: pointer;
  
  &:hover {  
    opacity: 0.8;
  }

  &:active, &:focus {
    outline: none;
    opacity: 1;
  }
  
 &::placeholder { 
    font-size: 16px;
    line-height: 20px;
    font-family: $font-main;
    font-weight: 400;
    color: $text-light;
  }

  &:focus::placeholder {
    color: transparent;
  }

  &:disabled { 
    box-shadow: 0 0 0 1px $red;
  }

  &.invalid {
    box-shadow: 0 0 0 1px $red;
  }
}