.advantage {
  background-color: $light-bg;
  padding-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 110px;
  text-align: center;
}

.advantage__title {
  font-weight: 400;
  font-family: $font-accent;
  line-height: 37px;
  font-size: 28px;
  margin-bottom: 70px;
}

.advantage__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  ;

  @media (max-width: 410px) {
    justify-content: space-between;
  }

  @media (max-width: 390px) {
    justify-content: center;
  }

  @media (min-width: 1200px) {
    justify-content: flex-start;
  }

  // &:nth-child(2n) {
  //   // margin-right: 0;
  //   background-color: red;
  // }
}

.advantage__item {
  width: 48%;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  padding: 20px;
  border-radius: 23px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-color: $white;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .25);

  @media (max-width: 410px) {
    margin-right: 0;
    width: 176px;
  }

  @media (min-width: 1000px) {
    text-align: left;
  }

  @media (min-width: 1200px) {
    max-width: 32%;
    margin-right: 2%;

    &:nth-child(3n) {
      margin-right: 0;
    }

  }
}

.advantage__num {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 5px;
  width: 82px;
  height: 82px;
  margin: 0 auto;
  margin-bottom: 20px;
  border-radius: 50%;

  font-family: $font-accent;
  line-height: 35px;
  font-size: 26px;
  color: $blue-dark;
  background-color: #f7f7f7;

  @media (min-width: 1000px) {
    text-align: left;
    margin-left: 0;
  }
}

.advantage__title-desc {
  width: 100%;
  line-height: 30px;
  font-size: 22px;
  font-weight: 400;
  margin: 0 auto;
  margin-top: 0;
  margin-bottom: 20px;
  color: $black;

  @media (min-width: 1000px) {
    width: 100%;
  }
}

.advantage__desc {
  width: 100%;
  margin: 0;
  margin-bottom: 20px;

  @media (min-width: 1000px) {
    width: 70%;
  }
}

@media (min-width: $tablet-width) {
  .advantage {
    padding-left: 50px;
    padding-right: 50px;
  }
}